















































































































































import {Component, Vue} from "vue-property-decorator";
import {Config} from "@/models/Config";
import {HttpService} from "@/services/ApiService";

@Component
export default class ConfigGlobal extends Vue{

  public delay = 0;

  public configData: Config ={
    android_version: '',
    ios_version: '',
    contra_entrega: 0,
    hour_init: 0,
    hour_fin: 0,
    hour_error: '',
    minutos_gracia: 0,
  }


  getConfigs(){
    HttpService.get(`configs` ,
        this.setConfig)
  }
  setConfigs(){
    HttpService.post(`configs/save` ,this.configData,
        this.setConfig)
  }

  setConfigsDelay(){
    HttpService.post(`configs/change/delays` ,{'delay': this.delay},
        this.setMessage)
  }

  setConfig(data: any){
    console.log(data.data)

      this.configData.android_version= data.data.android_version;
      this.configData.ios_version= data.data.ios_version;
      this.configData.contra_entrega= data.data.contra_entrega;
      this.configData.hour_init= data.data.hour_init;
      this.configData.hour_fin= data.data.hour_fin;
      this.configData.hour_error= data.data.hour_error;
      this.configData.minutos_gracia= data.data.minutos_gracia;


  }

  setMessage(data: any){
    console.log(data.data)
    if(data.alert){
      this.$alert(data.message, 'Exito', "success")
    }


  }

  created(){
    this.getConfigs()
  }


}
